a {
  text-decoration: none;
}

.ad {
  transition: 1s box-shadow;
  border-radius: 5px;
  overflow: hidden;
  width: 281px;
  height: 290px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.ad:hover {
  box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
  transition: 1s box-shadow;
}

.ad .btnHeart {
  background-color: transparent;
  border: none;
}

.ad .btnHeart svg {
  color: gray;
}

.ad .dateAd {
  width: 73px;
  font-size: 12px;
  overflow: hidden;
}

.ad .mainImg {
  width: 100%;
  height: 156px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  background-size: cover;
  transition: 1s  background-size;
  background-position: center;
}

.ad .mainImg:hover {
  transition: 1s  background-size;
}

.ad .lineAd {
  width: 100%;
  height: 3px;
  background-color: #59BA44;
  margin-top: -4px;
}

.ad .subCont {
  background-color: #FFFFFF;
  height: 133.5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
}

.ad .subCont .flexCont1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "FuturicaRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  margin-top: 17px;
}

.ad .subCont .flexCont1 p {
  color: black;
}

.ad .subCont .nameCostumer {
  font-family: "Futurica";
  color: rgba(150, 150, 150, 0.83);
  font-size: 14px;
  height: 300px !important;
  overflow: hidden;
  margin-bottom: 0px;
  margin-left: 3px;
}

.ad .subCont .flexCont2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Futurica;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
}

.ad .subCont .flexCont2 p {
  font-family: Futurica;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.5);
}

.ad .subCont .flexCont2 p img {
  margin-right: 3px;
}
