.homePage {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.topContainer {
  width: 100%;
  height: 730px;
  background: url(../../images/page/background/HomePageBG.png) no-repeat;
  background-size: 100%;
}

.mainBanner {
  width: 1083px;
  height: 193px;
  background: url(../../images/component/background/subBannerBg.jpg) no-repeat;
  background-size: 100% 100%;
  margin-top: 106px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 72px;
  font-family: "ComfortaaRegular";
  color: white;
}

.main {
  display: flex;
  margin-left: 29px;
  margin-top: 80px;
  align-self: flex-start;
}

.main .aside {
  width: 314px;
  height: 745px;
  background-color: red;
  background: #F6F7FB;
  position: sticky;
  display: flex;
  overflow-y: auto;
  overflow-x: hidden;
  flex-direction: column;
  position: -webkit-sticky;
  top: 130px;
  padding-left: 2%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 2%;
  box-shadow: 0px 4px 65px 19px rgba(0, 0, 0, 0.25);
  z-index: 3;
}

.main .aside::-webkit-scrollbar {
  width: 3px;
  background-color: white;
}

.main .aside::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.main .aside::-webkit-scrollbar-thumb {
  background-color: black;
  outline: 1px solid slategrey;
  height: 10px;
}

.main .aside img {
  width: 25px;
}

.main .aside .contForAside {
  padding-top: 11.5px;
  padding-bottom: 11.5px;
  border: none;
  background-color: transparent;
}

.main .aside .contForAside:focus {
  outline: none;
}

.main .aside .workTypeCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main .aside .workTypeCont p {
  width: 65%;
  margin: 0;
  display: flex;
  align-items: center;
  font-family: "ComfortaaRegular";
}

.main .aside .workTypeCont button {
  background: none;
  border: none;
  transform: scale(0.5);
}

.main .aside .workTypeCont button:focus {
  outline: none;
}

.main .aside .workTypeCont .threeDots {
  width: 30px;
  height: 30px;
  transform: scale(1);
  padding: 0 0 0 0 !important;
}

.main .aside .workTypeCont .threeDots img {
  width: 30px;
  height: 30px;
}

.main .aside .workTypeCont .differP {
  width: 85%;
}

.main .aside .workTypeCont img {
  width: 30px;
}

.main .aside .workDescCont {
  display: flex;
  align-items: center;
  flex-direction: column;
  display: none;
  background-color: none;
}

.main .aside .workDescCont p {
  margin: 0;
  margin-top: 20px;
  font-family: "FuturicaRegular";
}

.main .aside .workDescCont p a {
  color: black;
}

.main .allAdsContainer {
  margin-left: 100px;
  width: 1300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main .allAdsContainer .typeAddContainer {
  display: flex;
  align-self: flex-start;
  flex-direction: column;
  align-items: center;
  padding-bottom: 100px;
}

.main .allAdsContainer p {
  margin: 0;
}

.main .allAdsContainer .title {
  font-family: "ComfortaaRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 37px;
  line-height: 41px;
  align-self: flex-start;
  color: black;
}

.main .allAdsContainer .line1 {
  width: 110px;
  height: 2px;
  background-color: #5ABA42;
  margin-left: 65px;
  margin-top: 2px;
  align-self: flex-start;
}

.main .allAdsContainer .line2 {
  width: 60px;
  height: 2px;
  background-color: #F97922;
  margin-top: 2px;
  margin-left: 45px;
  align-self: flex-start;
}

.main .allAdsContainer .line3 {
  width: 70px;
  height: 2px;
  background-color: #F97922;
  margin-top: 2px;
  margin-left: 60px;
  align-self: flex-start;
}

.main .allAdsContainer .line4 {
  width: 220px;
  height: 2px;
  background-color: #5ABA42;
  margin-left: 65px;
  align-self: flex-start;
}

.main .allAdsContainer .line5 {
  width: 175px;
  height: 2px;
  background-color: #F97922;
  margin-left: 65px;
  align-self: flex-start;
}

.main .allAdsContainer .adsContainer {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 35px;
  margin-bottom: 50px;
}
