.cardMember {
  width: 214px;
  height: 159px;
  border-radius: 20px;
  padding: 8px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.cardMember .outline {
  width: 100%;
  height: 100%;
  border: 1px dashed #58BC40;
  border-radius: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.cardMember .outline img {
  margin-top: 17px;
  width: 60px;
}

.cardMember .outline hr {
  border: none;
  border-top: 1px dashed #58BC40;
  width: 95%;
  margin-top: 15px;
}

.cardMember .outline .title {
  margin: 0px;
  font-family: "ComfortaaRegular";
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 50px;
}
