.loginModal {
  width: 600px;
  height: 650px;
  margin-top: 100px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  background: url("../../images/page/background/login1.png");
  background-position: top;
  background-size: cover;
}

.loginModal form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loginModal .closeImg {
  align-self: flex-end;
  background-color: transparent;
  border: none;
  margin-top: 31px;
  margin-right: 31px;
  margin-bottom: 100px;
  filter: invert(1);
}

.loginModal .title {
  font-size: 36px;
  background: 0,37,46,1;
  margin: 0px;
}

.loginModal .errors {
  color: red;
  align-self: flex-start;
  margin-top: 10px;
  margin-left: 15px;
  font-size: 18px;
  font-weight: 700;
}

.loginModal .errorsDown {
  align-self: center;
  color: red;
  font-size: 18px;
  font-weight: 700;
}

.loginModal input {
  border: none;
  margin-top: 20px;
  height: 54px;
  width: 400px;
  padding-left: 30px;
  border-radius: 10px;
  font-size: 18px;
}

.loginModal input::placeholder {
  color: rgba(0, 0, 0, 0.4);
}

.loginModal input:focus {
  outline: none;
}

.loginModal .button {
  margin-top: 39px;
  width: 220px;
  align-self: center;
  height: 49px;
  font-family: "MsR";
}

.loginModal .button p {
  font-size: 20px;
  display: flex;
  justify-content: center;
}

.loginModal .link {
  margin: 0px;
  margin-top: 30px;
  font-size: 18px;
  color: white;
}

.loginModal .link button {
  background-color: transparent;
  border: none;
  color: #00fafa;
  font-size: 18px;
}
